import {IndexModel} from '../../model/index'
import {UserModel} from '../../model/user'
const indexModel = new IndexModel()
const userModel = new UserModel()
	export default {
		data() {
			return {
				choicePopup:false,
				failPopup:false,
				scale:18,
				title: 'map',
				address:'',
				latitude: 30.674958,
				longitude: 104.064283,
				markers: [],
				carList:[],
				choiceCar:""
			}
		},
		methods: {
			_to_vip_buy(){
				this.choicePopup = false;
				this.failPopup = false;
				uni.navigateTo({
					url:'/pagesC/vip_buy/vip_buy'
				})
			},
			// 地图放大
			scale_add(){
				this.scale = this.scale < 20 ? this.scale + 2 : 20
			},
			// 地图缩小
			scale_edd(){
				this.scale = this.scale > 4 ? this.scale - 2 : 4
			},
			// 停车
			parking()
			{
				userModel.getCars().then((res) => {
					this.carList = res.data
					if (!this.choiceCar) {
						this.choiceCar = res.data[0]
					}
					this.choicePopup = true
				})
			},
			// 选择汽车
			choice(item){
				this.choiceCar = item
			},
			// 确定
			choiceFinished(){
				this.choicePopup = false
				if (this.choiceCar.parking_car && this.choiceCar.parking_car.day_num > 0) {
					let params = {
						car_num:this.choiceCar.car_num,
						address:this.address,
						longitude:this.longitude,
						latitude:this.latitude,
					}
					console.log(params)
					indexModel.parking(params).then((res) => {
						uni.showToast({
							title : res.message,
							icon : 'none'
						})
					})
				} else {
					this.failPopup = true;
				}
			},
			closePopup(){
				this.choicePopup = false;
				this.failPopup = false;
			},
			
		},
		onShow(){
			 	let self = this
				uni.getLocation({
					type: 'wgs84',
					geocode:true,
					success: function (res) {
						// fixMe 获取address
						console.log(res)
						self.longitude = res.longitude
						self.latitude = res.latitude
						// this.address = res.address
						self.markers.push({
							iconPath :"https://imgcdn.tuogouchebao.com/location_icon.png",
							width:24,
							height:35,
							longitude:self.longitude,
							latitude:self.latitude,
						})
					}
				}); 
		}
	}
